    background-color: #ddd
}
#app{
    position: relative;
    margin: 20px;
    border: 1px dashed #ddd;
}
#clone,
#content{
    width: 200px;
    height: 200px;
    min-width: 200px;
    min-height: 200px;
    margin: 150px;
    padding: 80px;
    border: 10px solid #78BBE6;
    background: #fdfdf2;
    overflow: auto
}
#clone img,#content img{
    display: block;
    width: 420px;
}
#content{
    position: relative
}
#content:before{
    content: '';
    position: absolute;
    top: 80px;
    left: 80px;
    width: 200px;
    height: 200px;
    min-height: 200px;
    outline: 1px dashed #eee;
}
#content:after{
    content: '';
    display: block;
    clear: both;
}
#clone{
    position: absolute;
    top: 150px;
    left: 150px;
    margin: 0;
    opacity: 0.2;
    pointer-events: none;
    border-color: transparent;
    background: transparent;
    overflow: visible;
}
#overlay {
    text-align: center
}
#overlay,
    #overlay *{
    pointer-events: none;
}
#overlay div {
    position: absolute;
    width: 20px;
    height: 20px;
    overflow: visible;
    visibility: hidden;
    opacity: 0;
    -webkit-transition: opacity .3s;
    transition: opacity .3s;
}
#overlay div.active{
    visibility: visible;
    opacity: 1;
}
#overlay div:before, 
        #overlay div:after {
    position: absolute;
    display: block;
    z-index: 1;
}
#overlay div:before {
    content: attr(txt);
}
#overlay div:after {
    content: '';
}
#overlay div[t="width"]:before{
    width: 100%;
}
#overlay div[t="width"]:after{
    border-left: 1px dashed;
    border-right: 1px dashed;
    right: 0;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#overlay div[t="height"]:before{
    -webkit-writing-mode: vertical-rl;
        -ms-writing-mode: tb-rl;
            writing-mode: vertical-rl;
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
    height: 100%;
}
#overlay div[t="height"]:after{
    border-top: 1px dashed;
    border-bottom: 1px dashed;
    bottom: 0;
    height: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
#overlay div[t="width"][v="bottom"]:before,
            #overlay div[t="width"][v="bottom"]:after{
    bottom: 0;
}
#overlay div[t="height"][v="bottom"]:before,
            #overlay div[t="height"][v="bottom"]:after{
    right: 0;
}
#overlay #cssWidth{
    color: #1160AA;
    background: #D5EEFF;
}
#overlay #cssWidth:after {
    height: 410px;
}
#overlay #cssHeight{
    color: #1160AA;
    background: #D5EEFF;
}
#overlay #cssHeight:after {
    width: 410px;
}
#overlay #clientWidth{
    color: #F70776;
    background: #FFD6D6;
}
#overlay #clientWidth:after {
    height: 440px;
}
#overlay #clientHeight{
    color: #F70776;
    background: #FFD6D6;
}
#overlay #clientHeight:after {
    width: 440px;
}
#overlay #clientLeft{
    color: #57ACC5;
    background: #E8ECF1;
}
#overlay #clientLeft:after {
    height: 460px;
}
#overlay #clientTop{
    color: #57ACC5;
    background: #E8ECF1;
    // color: #45ADA8;
    // background: #E5FCC2;
}
#overlay #clientTop:after {
    width: 450px;
}
#overlay #offsetWidth{
    color: #11BFAE;
    background: #BFF4ED;
}
#overlay #offsetWidth:after {
    height: 470px;
}
#overlay #offsetHeight{
    color: #11BFAE;
    background: #BFF4ED;
}
#overlay #offsetHeight:after {
    width: 470px;
}
#overlay #scrollWidth{
    color: #F09872;
